import React, { useState } from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { CopyToClipboard } from 'react-copy-to-clipboard'

import css from "./Address.module.css"

export interface AddressProps {
    children: string;
    chainId: number;
}

const Address: React.FC<AddressProps> = ({children: address, chainId}) => {
    const [hover1, setHover1] = useState(false);
    const [down1, setDown1] = useState(false);
    const [hover2, setHover2] = useState(false);
    const [down2, setDown2] = useState(false);
    const { button, buttonHover, buttonDown } = css
    const className1 = down1 ? buttonDown : hover1 ? buttonHover : button
    const className2 = down2 ? buttonDown : hover2 ? buttonHover : button
    const url = chainId === 80001 ? "http://mumbai.polygonscan.com/address" : "http://polygonscan.com/address"

    if(!address) {
        return null
    }
    return <span>
        <span className={css.address}><code>{address}</code></span>
        &nbsp;
        {address && <CopyToClipboard text={address}>
            <span
                onPointerOver={()=> setHover1(true)}
                onPointerOut={() => setHover1(false)}
                onPointerDown={()=> setDown1(true)}
                onPointerUp={() => setDown1(false)}
            >
                <button className={className1}><FontAwesomeIcon title={"Copy Address: "+address} icon={["far", "copy"]}/></button>
            </span>
            </CopyToClipboard>
        }
        <span
            onPointerOver={()=> setHover2(true)}
            onPointerOut={() => setHover2(false)}
            onPointerDown={()=> setDown2(true)}
            onPointerUp={() => setDown2(false)}
        >
            {<a title={url+"/"+address} href={url+"/"+address} target="_blank" rel="noopener noreferrer"><button className={className2}><FontAwesomeIcon icon="external-link-alt"/></button></a>}
        </span>
    </span>
}

export default Address
